import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HistoryPaymentComponent } from './history-payment.component';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { NzTabsModule } from 'ng-zorro-antd/tabs'; 
import { HistoryComponent } from './history/history.component';
import { NzTableModule } from 'ng-zorro-antd/table';
import { DetailHistoryComponent } from './history/detail-history/detail-history.component';


const routes: Routes = [
  {
    path: '',
    component: HistoryPaymentComponent
  },
  {
    path:'detail',
    component: DetailHistoryComponent
  }
]


@NgModule({
  declarations: [
    HistoryPaymentComponent,
     
    HistoryComponent,
    DetailHistoryComponent
  ],
  imports: [
    CommonModule,
    TranslateModule,
    NgZorroAntModule,
    RouterModule.forChild(routes),
    NzTabsModule,
    NzTableModule
  ]
})
export class HistoryPaymentModule { }
